import React, { Component } from "react";
import { Icon, Tooltip } from "antd";
import ReactMarkdown from "react-markdown";
import { PrismLight as SyntaxHighlighter } from "react-syntax-highlighter";
// import jsx from "react-syntax-highlighter/dist/esm/languages/prism/jsx";
import coy from "react-syntax-highlighter/dist/esm/styles/prism/coy";
import Clipboard from "react-clipboard.js";

class CodePreviewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cliped: false
    };
  }
  componentWillMount() {
    // SyntaxHighlighter.registerLanguage("jsx", jsx);
  }
  onClipSuccess = () => {
    this.setState({
      cliped: true
    });
  };
  onClipLeave = () => {
    setTimeout(() => {
      this.setState({
        cliped: false
      });
    }, 500);
  };
  render() {
    const { children, note, code } = this.props;
    return (
      <div className="code-preview-container">
        <div className="cp-component">
          <div className="component-header">
            <ReactMarkdown escapeHtml={false} source={note} />
          </div>
          <div className="component-content">{children}</div>
        </div>
        <div className="cp-code">
          <div className="code-header">
            <Clipboard className="copy" data-clipboard-text={code} onSuccess={this.onClipSuccess}>
              <Tooltip title={this.state.cliped ? "已复制" : "复制到剪切板"} onMouseLeave={this.onClipLeave}>
                <span>复制</span>
              </Tooltip>
            </Clipboard>
          </div>
          {/* <div className="code-tabs">code tabs</div> */}
          <div className="code-content">
            <SyntaxHighlighter language="jsx" style={coy}>
              {code}
            </SyntaxHighlighter>
          </div>
        </div>
        {/* <div>sdfsdf</div> */}
      </div>
    );
  }
}

export default CodePreviewer;
